import { call, fork, put, select, take, takeEvery } from 'redux-saga/effects';
import * as uuid from 'uuid/v4';
import * as analysisActions from '../analysis/actions';
import * as clusterActions from "../cluster/actions";
import { AnalysisTypes, DatasetTypes, OpenDatasetResponse } from '../messages';
import { RootReducer } from '../store';
import * as datasetActions from "./actions";
import { openDataset } from './api';


export function* createDatasetSaga(action: ReturnType<typeof datasetActions.Actions.create>) {
    try {
        const resp: OpenDatasetResponse = yield call(openDataset, action.payload.dataset.id, { dataset: action.payload.dataset });
        if (resp.status === "ok") {
            yield put(datasetActions.Actions.created(resp.details));
        } else if (resp.status === "error") {
            const timestamp = Date.now();
            yield put(datasetActions.Actions.error(resp.dataset, resp.msg, timestamp));
        }
    } catch (e) {
        const timestamp = Date.now();
        yield put(datasetActions.Actions.error(action.payload.dataset.id, `Error loading dataset: ${e.toString()}`, timestamp));
    }
}

export function* loadExampleDataSaga() {
    while (true) {
        yield take(clusterActions.ActionTypes.CONNECTED);
        const state: RootReducer = yield select();

        if (true) {
            yield call(function* () {
                const existing = state.dataset.ids.map(id => state.dataset.byId[id])
                    .filter(ds => ds.params.type === DatasetTypes.HDF5
                        && ds.params.path === "/tmp/scan_11_x256_y256_int.emd");
                let dataset = uuid();
                if (existing.length === 0) {
                    yield put(datasetActions.Actions.create({
                        id: dataset,
                        params: {
                            type: DatasetTypes.HDF5,
                            path: "/tmp/scan_11_x256_y256_int.emd",
                            tileshape: [1, 8, 128, 128],
                            name: "int dataset",
                            dsPath: "experimental/science_data/data",
                        }
                    }))
                    yield take(datasetActions.ActionTypes.CREATED);
                } else {
                    dataset = existing[0].id;
                }
                yield put(analysisActions.Actions.create(
                    dataset, AnalysisTypes.APPLY_RING_MASK
                ));
            });

            yield call(function* () {
                const existing = state.dataset.ids.map(id => state.dataset.byId[id])
                    .filter(ds => ds.params.type === DatasetTypes.MIB
                        && ds.params.filesPattern === "/home/clausen/Data/Merlin/*.mib");
                let dataset = uuid();
                if (existing.length === 0) {
                    yield put(datasetActions.Actions.create({
                        id: dataset,
                        params: {
                            type: DatasetTypes.MIB,
                            filesPattern: "/home/clausen/Data/Merlin/*.mib",
                            tileshape: [1, 8, 256, 256],
                            scanSize: [6, 512],
                            name: "mib test dataset",
                        }
                    }))
                    yield take(datasetActions.ActionTypes.CREATED);
                } else {
                    dataset = existing[0].id;
                }
                yield put(analysisActions.Actions.create(
                    dataset, AnalysisTypes.APPLY_RING_MASK
                ));
            })
        }
    }
}

export function* datasetRootSaga() {
    yield takeEvery(datasetActions.ActionTypes.CREATE, createDatasetSaga);
    yield fork(loadExampleDataSaga);
}


